$height: 2rem;
$sun-color: #fcd22c;
$moon-color: #d4d5e2;
$day-sky: #439ee1;
$night-sky: #212536;
$moon-elem: #a3b4c3;

.theme-switch {
  display: grid;
  place-items: center;

  .switch-wrapper {
    width: $height * 2.5;
    height: $height;
    border-radius: $height;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding: 0 0.2rem;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    container-type: inline-size;
    transition: background 1s ease;

    &::before {
      position: absolute;
      width: 100%;
      height: 100%;
      content: "";
      top: 0;
      left: 0;
      z-index: 4;
      border-radius: $height;
      box-shadow: 0 0 5px 6px rgba(0, 0, 0, 0.05) inset;
    }

    z-index: 3;

    &.day {
      background: $day-sky;

      .clouds-line-1,
      .clouds-line-2 {
        transform: translateY(0%);
        transition: all 1s ease;
        z-index: 1;
      }

      .clouds-line-2 {
        transition-delay: 40ms;
      }
    }

    &.night {
      background: $night-sky;

      .clouds-line-1,
      .clouds-line-2 {
        transform: translateY(150%);
        transition: all 0.5s cubic-bezier(0.56, -0.02, 0.64, 1);
      }

      .clouds-line-2 {
        transition-delay: 40ms;
      }
    }

    .sun-moon-combo {
      height: $height - 0.2rem;
      width: $height - 0.2rem;
      border-radius: 50%;
      z-index: 3;
      position: absolute;
      transition: all 0.8s cubic-bezier(0.56, -0.02, 0.64, 1);
      transform: rotate(-90deg);
      filter: drop-shadow(-10px 2px 5px rgba(0, 0, 0, 0.2));

      &.sun {
        background: $sun-color;
        box-shadow: inset -3px 0.6px 2px #fcf66b, inset 5px 0px 2px #e2a43d;
      }

      .moon-elem {
        width: 0.1rem;
        height: 0.1rem;
        content: "";
        border-radius: 50%;
        background: $moon-elem;
        box-shadow: inset 0 1px 1px darken($moon-elem, 20%);
        opacity: 0;

        &:nth-child(1) {
          left: 0.1rem;
          transform: scale(0);
        }

        &:nth-child(2) {
          top: 0.1rem;
          transform: scale(0);
        }

        &:nth-child(3) {
          bottom: 0.3rem;
          right: 0.3rem;
          transform: scale(0);
        }
      }

      &.moon {
        background: $moon-color;
        transform: translateX(calc(100cqw - 100%));
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: inset 0 -4px 2px darken($moon-elem, 20%),
        inset 0 5px 2px rgba(255, 255, 255, 0.9);

        .moon-elem {
          width: 0.3rem;
          height: 0.3rem;
          content: "";
          border-radius: 50%;
          background: $moon-elem;
          box-shadow: inset 0 1px 1px darken($moon-elem, 20%);
          opacity: 1;
          transition: opacity 2s ease, transform 1s ease, left 1s ease,
          top 1s ease, bottom 1.5s ease;
          position: absolute;

          &:nth-child(1) {
            left: 0.3rem;
            transform: scale(2.2);
          }

          &:nth-child(2) {
            top: 0.2rem;
            transform: scale(1.3);
          }

          &:nth-child(3) {
            bottom: 0.5rem;
            right: 0.4rem;
            transform: scale(1.7);
          }
        }
      }
    }

    .clouds-line-1,
    .clouds-line-2 {
      position: absolute;
      height: calc(#{$height} / 1.5);
      width: calc(#{$height} / 1.5);
      border-radius: 50%;
      top: 0.8rem;
      right: -0.8rem;
      background: #d1ecfc;
      box-shadow: -4px 4px 1px #d1ecfc, -10px 10px 1px #d1ecfc,
      -14px 10px 1px #d1ecfc, -16px 9px 1px #d1ecfc,
      -20px 8px 1px #d1ecfc, -35px 14px 1px #d1ecfc,
      -50px 10px 1px #d1ecfc, -70px 8px 1px #d1ecfc,
      -80px 5px 1px #d1ecfc;
      z-index: 1;
      transform: translateY(0);
    }

    .clouds-line-2 {
      top: 0;
      right: -0.8rem;
      opacity: 0.5;
    }

    .raybase,
    .raybase-2 {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      z-index: 2;

      .ray-1 {
        width: $height + 1;
        height: $height + 1;
        background: #fff;
        content: "";
        opacity: 0.15;
        border-radius: 50%;
        position: absolute;
        transform-origin: left;
      }

      .ray-2 {
        width: $height + 2;
        height: $height + 2;
        background: #fff;
        content: "";
        opacity: 0.2;
        border-radius: 50%;
        position: absolute;
        transform-origin: left;
      }

      .ray-3 {
        width: $height + 3;
        height: $height + 3;
        background: #fff;
        content: "";
        opacity: 0.3;
        border-radius: 50%;
        position: absolute;
        transform-origin: left;
      }
    }

    .raybase {
      left: calc(#{$height} / 2);

      &.hide {
        @for $i from 1 through 3 {
          transition: all 1s ease;
          .ray-#{$i} {
            transform: scale(0);
            transition: transform 1s ease, opacity 0.5s ease;
            transition-delay: calc(0.1 / 3) * $i + s;
            opacity: 0;
            transform-origin: left;
          }
        }
      }

      &.show {
        left: calc(#{$height} / 2);
        @for $i from 1 through 3 {
          transition: all 1s ease 0.6s;
          .ray-#{$i} {
            transform: scale(1);
            transition: transform 1s ease, opacity 1.5s ease;
            transition-delay: calc(0.1 / 3) * $i + s;
            opacity: 0.2;
            transform-origin: left;
          }
        }
      }
    }

    .raybase-2 {
      right: calc(#{$height} / 2);

      &.hide {
        @for $i from 1 through 3 {
          transition: all 1s ease;
          .ray-#{$i} {
            transform: scale(0);
            transition: transform 1s ease, opacity 0.5s ease;
            transition-delay: calc(0.1 / 3) * $i + s;
            opacity: 0;
            transform-origin: right;
          }
        }
      }

      &.show {
        right: calc(#{$height} / 2);
        @for $i from 1 through 3 {
          transition: all 1s ease 0.6s;
          .ray-#{$i} {
            transform: scale(1);
            transition: transform 1s ease, opacity 1.5s ease;
            transition-delay: calc(0.1 / 3) * $i + s;
            opacity: 0.2;
            transform-origin: right;
          }
        }
      }
    }

    .stars {
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 1;

      .star {
        width: 0.5rem;
        height: 0.5rem;
        background: rgba(255, 255, 255, 0.7);
        content: "";
        position: absolute;
        left: 1rem;
        top: 1rem;
        z-index: 1;
        clip-path: polygon(
                        37% 37%,
                        50% 0,
                        60% 38%,
                        99% 47%,
                        58% 63%,
                        47% 100%,
                        37% 62%,
                        0 46%
        );

        &:nth-child(1) {
          top: 0.5rem;
          left: 0.3rem;
          animation-delay: 0.1s;
          animation: sparkling 0.5s linear infinite;
        }

        &:nth-child(2) {
          top: 0.1rem;
          left: 1.5rem;
          animation-delay: 0.2s;
          animation: sparkling 0.5s linear infinite;
        }

        &:nth-child(3) {
          top: 1.5rem;
          left: 1.2rem;
          position: absolute;
          animation: sparkling 0.5s linear infinite;
          animation-delay: 0.3s;
        }
      }

      .round-star {
        width: 0.5rem;
        height: 0.5rem;
        background: rgba(255, 255, 255, 0.7);
        content: "";
        position: absolute;
        z-index: 1;
        border-radius: 50%;
        @for $i from 1 through 15 {
          &:nth-of-type(#{$i}) {
            top: random(3) - 1 + rem;
            left: random(3) - 1 + rem;
            width: random(2) + px;
            height: random(2) + px;
            opacity: calc(random(50) / 100) + 0.5;
            animation: sparkling 0.5s linear infinite;
            animation-delay: calc(1 / 20) * $i + s;
          }
        }
      }
    }
  }
}

@keyframes sparkling {
  0% {
    box-shadow: 0 0 0px rgba(255, 255, 255, 0.5);
    transform: scale(0.9);
  }
  50% {
    box-shadow: 0 0 10px 0.6px rgba(255, 255, 255, 0.9);
    transform: scale(1.1);
  }
}
